import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import data from '../data'
import {Link} from "react-router-dom";
import Chip from "@material-ui/core/Chip";

const styles = theme => ({
    root: {
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
    listItem: {
        borderBottom: '1px solid rgba(0, 0, 0, 0.12)',
        '&:last-child': {
            border: 0,
        },
    },
    inline: {
        display: 'inline',
    },
});

function ListComponent(props) {
    const { classes } = props;
    let array = props.thisSC ? data.SC.arrow : data.orders.arrow;

    if (props.thisSC && props.filters.confirmation) {
        array = array.filter(item => item.confirmation)
    }

    if (!props.thisSC && props.filters.type !== 'all') {
        array = array.filter(item => item.type === props.filters.type)
    }

    if (!props.thisSC && props.filters.result !== 'all') {
        array = array.filter(item => item.result === props.filters.result)
    }

    if (props.category !== 'all') {
        array =  array.filter(item => item.state === props.category)
    }

    return (
        <List className={classes.root}>
            {
                array.map(item => {
                    return (
                        <ListItem button className={classes.listItem} alignItems="flex-start">
                            <ListItemText
                                key={item.id}
                                primary={props.thisSC ? `Заявка №${item.id} от ${item.date}` : `Заказ-наряд. Заявка №${item.id} от ${item.date}`}
                                secondary={
                                    <React.Fragment>
                                        Внешний номер: {item.external__id}
                                    </React.Fragment>
                                }
                            />
                        </ListItem>
                    )
                })
            }
        </List>
    );
}

ListComponent.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ListComponent);