import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import {withStyles} from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import ListComponent from './ListComponent';
import Filtres from "./Filters";
import Paper from "@material-ui/core/Paper";
import {Link} from 'react-router-dom'

const styles = theme => ({
    root: {
        width: '100%',
        display: 'flex',
        minHeight: '100vh',
    },
    main: {
        flex: 1,
        marginRight: theme.spacing.unit * 9,
        padding: theme.spacing.unit * 4,
        paddingRight: 0,
    },
    chip: {
        margin: theme.spacing.unit / 2,
    },
    toolbar: theme.mixins.toolbar,
    filters: {
        width: theme.spacing.unit * 40,
    },
});

class Main extends React.Component {
    state = {
        confirmation: false,
        type: 'all',
        result: 'all',
    }

    onClickChip = () => {
        this.setState({
            active: this.props.match.params.category ? this.props.match.params.category : '',
        })
    }

    handleChange = name => event => {
        if (name === 'confirmation') {
            this.setState({confirmation: !this.state.confirmation})
        } else {
            this.setState({ [name]: event.target.value });
        }

    }

    render() {
        const {
            props: {
                classes,
                match: {path, params},
            },
            onClickChip,
        } = this;

        const thisSC = path.indexOf('SC') > -1


        const chipsArray = thisSC ?
            [
                {text: 'Все', indx: 'all'},
                {text: 'Созданы', indx: 'create'},
                {text: 'В работе', indx: 'inwork'},
                {text: 'Согласование ремонта', indx: 'pre_repair'},
                {text: 'Исполнение ремонта', indx: 'repair'},
                {text: 'Закрыты', indx: 'close'},
                {text: 'Отменены', indx: 'cancel'}
            ] :
            [
                {text: 'Все', indx: 'all'},
                {text: 'Созданы', indx: "create"},
                {text: 'В работе', indx: 'inwork'},
                {text: 'Контроль качества', indx: 'control'},
                {text: 'Выполнены', indx: 'done'},
                {text: 'Закрыты', indx: 'close'},
                {text: 'Отменены', indx: 'cancel'}
            ];

        return (
            <div className={classes.root}>
                <main className={classes.main}>
                    <div className={classes.toolbar} />
                    <Typography variant="h3">
                        {thisSC ? 'Список заявок' : 'Список заказ-нарядов'}
                    </Typography>
                    {
                        chipsArray.map(({text, indx}) => {
                            return (
                                <Link to={thisSC ? `/SC/${indx}` : `/orders/${indx}`}>
                                    {
                                        indx === params.category ?
                                            <Chip
                                                label={text}
                                                key={indx}
                                                onClick={() => onClickChip(indx)}
                                                className={classes.chip}
                                                variant="outlined"
                                                color='primary'
                                            /> :
                                            <Chip
                                                label={text}
                                                key={indx}
                                                onClick={() => onClickChip(indx)}
                                                className={classes.chip}
                                                variant="outlined"
                                            />
                                    }
                                </Link>
                            )
                        })
                    }
                    <ListComponent
                        thisSC={thisSC}
                        category={params.category}
                        filters={this.state}
                    />
                </main>
                <Paper className={classes.filters}>
                    <div className={classes.toolbar} />
                    <Filtres
                        handleChange={this.handleChange}
                        valueType={this.state.type}
                    valueResult={this.state.result}
                        thisSC={thisSC} category={params.category}/>
                </Paper>
            </div>
        );
    }
}


Main.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Main);